<template>
  <div class="flex-column">
    <div class="p3">
      <a class="tertiary font-2 uppercase" @click="signOut"><i class="fas fa-sign-out-alt fa-rotate-180 m1-right"></i>logout</a>
    </div>

    <div class="flex-one p3 p1-top" :class="[isShowingModal ? 'scrolly-notouch' : 'scrolly']">
      <div v-if="property" class="m4-bottom">
        <p class="font-title light font3">{{property.description}}</p>
        <p class="tertiary line180">{{property.city}}, {{property.state}} ({{property.county}})</p>
      </div>

      <div v-if="showForms" class="forms">
        <title-label title="Disclosure Forms" class="m4-top"></title-label>
        <div class="form-item flex-one border-base2 m2-bottom">
          <p class="bg-secondary base font2 bold font-title p1 p2-left"><i class="fas fa-poll-h m1-right"></i>TDS</p>
          <p class="p2 text-center" v-if="!tds"><a @click="fillForm('tds')">Click to complete TDS Form</a></p>
          <div v-if="tds" class="flex-row text-center">
            <p class="p2 flex-one border-right-base2"><a @click="showPdf(tds.Key)">VIEW</a></p>
            <p class="p2 flex-one"><a @click="fillForm('tds')">EDIT</a></p>
          </div>
        </div>

        <div class="form-item flex-one border-base2">
          <p class="bg-secondary base font2 bold font-title p1 p2-left"><i class="fas fa-poll-h m1-right"></i>SSC</p>
          <p class="p2 text-center" v-if="!ssc"><a @click="fillForm('ssc')">Click to complete SSC Form</a></p>
          <div v-if="ssc" class="flex-row text-center">
            <p class="p2 flex-one border-right-base2"><a @click="showPdf(ssc.Key)">VIEW</a></p>
            <p class="p2 flex-one"><a @click="fillForm('ssc')">EDIT</a></p>
          </div>
        </div>
      </div>

      <div v-if="showAppointments" class="appointments">
        <title-label title="Appointments"></title-label>
        <div v-for="appointment in appointments" :key="appointment.id" class="appointment-wrapper" :class="[appointment.status]">
          <h3 class="uppercase tertiary2 m4-top m1-bottom font-title"><span>{{appointment.status}}</span></h3>
          <appointment :appointment="appointment"
            :comments="apptComments(appointment.id)"
            class="m2-bottom"
            @change="apptChanged"
            @comment="commentAdded"
            @showDetails="showingModal"></appointment>
        </div>
        <p v-if="appointments.length === 0" class="tertiary">No appointments</p>
      </div>
    </div>

    <div class="tab-bar flex-row bg-secondary base shadow-top ">
      <div class="flex-one flex-column text-center p1 border-right-primary" :class="{active: showAppointments}" @click="mode = 'appt'">
        <p class="m1-bottom"><i class="fas fa-clock" /></p>
        <p class="font-2 uppercase">Appointments</p>
      </div>
      <div class="flex-one flex-column text-center p1 border-left-primary" :class="{active: showForms}" @click="mode = 'forms'">
        <p class="m1-bottom"><i class="fas fa-file-alt" /></p>
        <p class="font-2 uppercase">Forms</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import TitleLabel from '@/components/TitleLabel'
import Appointment from '@/views/Appointment'
import sort from 'fast-sort'
import moment from 'moment'

export default {
  name: 'Client',
  components: { TitleLabel, Appointment },
  data () {
    return {
      mode: 'appt',
      appointments: [],
      isShowingModal: false,
      property: undefined,
      files: [],
      comments: []
    }
  },
  props: ['clientId'],
  inject: ['userClient', 'apptHelper', 'commentsClient'],
  computed: {
    ...mapGetters(['session']),
    showAppointments () {
      return this.mode === 'appt'
    },
    showForms () {
      return this.mode === 'forms'
    },
    tds () {
      return this.files.find(f => f.Key.includes('tds.pdf'))
    },
    ssc () {
      return this.files.find(f => f.Key.includes('ssc.pdf'))
    }
  },
  methods: {
    ...mapActions(['setSession', 'setPdf']),
    signOut () {
      this.setSession(undefined)
      this.$router.push('/')
    },
    showPdf (key) {
      this.setPdf(this.userClient.getFileUrl(this.clientId, key))
      this.$router.push(`/pdf`)
    },
    showingModal (isShowing) {
      this.isShowingModal = isShowing
    },
    fetchAppointments () {
      this.userClient.getAppointments(this.clientId)
        .then(appointments => {
          this.appointments = appointments
          this.apptHelper.sortAppointments(this.appointments)
        })
    },
    fetchProperty () {
      this.userClient.getProperty(this.clientId)
        .then(property => {
          this.property = property
        })
    },
    fetchFiles () {
      this.userClient.getFiles(this.clientId)
        .then(files => {
          this.files = files
        })
    },
    async fetchComments () {
      this.comments = await this.commentsClient.getComments(this.clientId)
      sort(this.comments).desc('createdat')
    },
    fillForm (form) {
      let m = moment(new Date())
      const prepopulate = {
        property_city: this.property.city,
        property_county: this.property.county,
        property_name: this.property.description,
        property_described_as: this.property.description,
        property_as_of_date: m.format('MM/DD'),
        property_as_of_year: m.format('YYYY'),
        property_date: m.format('MM/DD/YYYY')
      }
      const state = encodeURIComponent(JSON.stringify(prepopulate))
      this.$router.push(`/disclosures?form=${form}&state=${state}`)
    },
    apptComments (appointmentId) {
      return this.apptHelper.findComments(appointmentId, this.comments)
    },
    commentAdded (comment) {
      this.comments.push(comment)
      sort(this.comments).desc('createdat')
    },
    apptChanged (updatedAppointment) {
      const index = this.appointments.findIndex(a => a.id === updatedAppointment.id)
      this.appointments[index] = updatedAppointment
      this.apptHelper.sortAppointments(this.appointments)
    }
  },
  mounted () {
    if (this.$route.query.forms !== undefined) {
      this.mode = 'forms'
    }
    this.fetchAppointments()
    this.fetchProperty()
    this.fetchFiles()
    this.fetchComments()
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/sass/app";

.form-item {
  border-top-left-radius: 10px;
  overflow: hidden;
}
.appointments {
  .pending h3 span { border-bottom: solid 1px #24c1ff; }
  .scheduled h3 span { border-bottom: solid 1px #0461c2; }
  .completed h3 span { border-bottom: solid 1px #767569; }
  .canceled h3 span { border-bottom: solid 1px #fc0000; }
  .declined h3 span { border-bottom: solid 1px #ff9d10; }
  .pending ~ .pending, .scheduled ~ .scheduled, .declined ~ .declined, .completed ~ .completed, .canceled ~ .canceled {
    h3 {
      display: none;
    }
  }
}
.tab-bar {
  div:not(.active) {
    opacity: 0.5;
  }
  .active {
    box-shadow: inset 1px 0 10px $primary;
  }
}
</style>
